


.wp-caption.aligncenter {
    margin: 0 auto 20px;
    text-align: center;
}
.wp-caption.alignleft {
    margin: 0 auto 20px;
    text-align: center;
    @include media(">767px") {
        float: left;
        margin: 0 20px 20px 0;
        text-align: left;
    }
}
.wp-caption.alignright {
    margin: 0 auto 20px;
    text-align: center;
    @include media(">767px") {
        float: right;
        margin: 0 0 20px 20px;
        text-align: right;
    }
}



img.aligncenter {
    display: flex;
    margin: 0 auto 20px;
}
img.alignleft {
    display: flex;
    margin: 0 auto 20px;
    @include media(">767px") {
        float: left;
        margin: 0 20px 20px 0;
    }
}
img.alignright {
    display: flex;
    margin: 0 auto 20px;
    @include media(">767px") {
        float: right;
        margin: 0 0 20px 20px;
    }
}



