.commentlist {
    li {
        &:before {
            display: none;
        }
    }
    .comment-container {
        margin: 0 0 20px;
    }
    .perma {
        float: right;
        margin: 0 0 0 20px;
    }
    .avatar {
        float: left;
        margin: 0 20px 20px 0;
    }
    .comment-entry {
        overflow: hidden;
        width: 100%;
        .reply {
            text-align: right;
        }
    }
}

.comment-form {
    label {
        display: block;
    }
}

